import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import './dropdown.css'

function DropDown() {

    const [click, setClick] = useState(false)

    const handleClick = () => setClick(!click)

    return (
        <>
                <div onClick={handleClick} className={click ? 'drop-menu' : 'dropdown-menu'}>
                    <Link className='menu-items' to="/hkmcclib" onClick={() => setClick(false)}>
                        Search
                    </Link>
                    <Link className='menu-items' to="/hkmcclib/checkOut" onClick={() => setClick(false)}>
                        Check Out
                    </Link>
                </div>
        </>
    )
}

export default DropDown;
